// ============================================
// RISIVO CRM - Phone Number Management API
// Search, purchase, and manage phone numbers
// ============================================

import { NextRequest, NextResponse } from 'next/server'
import { supabaseAdmin as supabase } from '@/lib/supabase/server'
import { twilioService } from '@/lib/telephony/twilio-service'
import type { PhoneNumber, AgencyTwilioConfig, PhoneNumberType } from '@/lib/telephony/types'

// ==========================================
// GET - List phone numbers or search available
// ==========================================

export async function GET(request: NextRequest) {
  try {
    const { searchParams } = new URL(request.url)
    const action = searchParams.get('action') // 'list' (default) or 'search'
    const subAccountId = searchParams.get('subAccountId')

    if (action === 'search') {
      // Search for available phone numbers from Twilio
      const country = searchParams.get('country') || 'US'
      const type = (searchParams.get('type') || 'local') as PhoneNumberType
      const areaCode = searchParams.get('areaCode') || undefined
      const locality = searchParams.get('locality') || undefined
      const region = searchParams.get('region') || undefined
      const contains = searchParams.get('contains') || undefined
      const limit = parseInt(searchParams.get('limit') || '20')

      // Get agency config
      const { data: agencyConfig } = await supabase
        .from('AgencyTwilioConfig')
        .select('*')
        .is('agencyId', null)
        .single()

      if (!agencyConfig) {
        return NextResponse.json(
          { success: false, error: 'Twilio configuration not found' },
          { status: 400 }
        )
      }

      await twilioService.initialize(agencyConfig as AgencyTwilioConfig)

      const availableNumbers = await twilioService.searchAvailableNumbers(
        country,
        type,
        { areaCode, locality, region, contains, limit }
      )

      // Add markup to prices
      const numbersWithMarkup = availableNumbers.map(num => {
        const markup = agencyConfig.usageMarkupPercent || 5
        return {
          ...num,
          monthlyPriceWithMarkup: num.monthlyPrice * (1 + markup / 100),
          setupPriceWithMarkup: num.setupPrice * (1 + markup / 100),
        }
      })

      return NextResponse.json({ success: true, data: numbersWithMarkup })
    }

    // List phone numbers for sub-account
    if (!subAccountId) {
      return NextResponse.json(
        { success: false, error: 'Sub-account ID is required for listing' },
        { status: 400 }
      )
    }

    const { data, error } = await supabase
      .from('PhoneNumber')
      .select('*')
      .eq('subAccountId', subAccountId)
      .order('isPrimary', { ascending: false })
      .order('createdAt', { ascending: false })

    if (error) throw error

    return NextResponse.json({ success: true, data })
  } catch (error) {
    console.error('Error in phone numbers GET:', error)
    return NextResponse.json(
      { success: false, error: 'Failed to get phone numbers' },
      { status: 500 }
    )
  }
}

// ==========================================
// POST - Purchase a new phone number
// ==========================================

export async function POST(request: NextRequest) {
  try {
    const body = await request.json()

    if (!body.subAccountId) {
      return NextResponse.json(
        { success: false, error: 'Sub-account ID is required' },
        { status: 400 }
      )
    }

    if (!body.phoneNumber && !body.type) {
      return NextResponse.json(
        { success: false, error: 'Phone number or type is required' },
        { status: 400 }
      )
    }

    // Get Twilio subaccount for this CRM sub-account
    const { data: twilioSubaccount, error: subaccountError } = await supabase
      .from('TwilioSubaccount')
      .select('*')
      .eq('subAccountId', body.subAccountId)
      .single()

    if (subaccountError || !twilioSubaccount) {
      return NextResponse.json(
        { success: false, error: 'Twilio subaccount not found. Please provision telephony first.' },
        { status: 400 }
      )
    }

    if (twilioSubaccount.status !== 'active') {
      return NextResponse.json(
        { success: false, error: `Cannot purchase number - subaccount is ${twilioSubaccount.status}` },
        { status: 400 }
      )
    }

    // Get agency config
    const { data: agencyConfig } = await supabase
      .from('AgencyTwilioConfig')
      .select('*')
      .is('agencyId', null)
      .single()

    if (!agencyConfig) {
      return NextResponse.json(
        { success: false, error: 'Twilio configuration not found' },
        { status: 400 }
      )
    }

    await twilioService.initialize(agencyConfig as AgencyTwilioConfig)

    // Determine webhook base URL
    const webhookBaseUrl = agencyConfig.webhookBaseUrl || process.env.NEXT_PUBLIC_APP_URL || ''

    // Check if this is the first number (will be primary)
    const { count: existingCount } = await supabase
      .from('PhoneNumber')
      .select('id', { count: 'exact', head: true })
      .eq('subAccountId', body.subAccountId)
      .eq('status', 'active')

    const isPrimary = (existingCount || 0) === 0

    // Purchase the number
    const result = await twilioService.purchaseNumberForSubaccount(
      twilioSubaccount.twilioAccountSid,
      twilioSubaccount.twilioAuthToken,
      {
        subAccountId: body.subAccountId,
        agencyId: body.agencyId || twilioSubaccount.agencyId || '',
        type: body.type || 'local',
        country: body.country || 'US',
        areaCode: body.areaCode,
        locality: body.locality,
        contains: body.contains,
        friendlyName: body.friendlyName,
        assignedFor: body.assignedFor || 'all',
        initiatedByUserId: body.initiatedByUserId || body.userId || 'system',
        initiatedByUserName: body.initiatedByUserName,
      },
      webhookBaseUrl
    )

    if (!result.success || !result.phoneNumber) {
      return NextResponse.json(
        { success: false, error: result.error || 'Failed to purchase phone number' },
        { status: 500 }
      )
    }

    // Save to database
    const phoneData: Partial<PhoneNumber> = {
      ...result.phoneNumber,
      twilioSubaccountId: twilioSubaccount.id,
      isPrimary,
      isDefault: isPrimary,
    }

    const { data: savedPhone, error: saveError } = await supabase
      .from('PhoneNumber')
      .insert(phoneData)
      .select()
      .single()

    if (saveError) throw saveError

    return NextResponse.json({ success: true, data: savedPhone }, { status: 201 })
  } catch (error) {
    console.error('Error purchasing phone number:', error)
    return NextResponse.json(
      { success: false, error: 'Failed to purchase phone number' },
      { status: 500 }
    )
  }
}

// ==========================================
// PUT - Update phone number configuration
// ==========================================

export async function PUT(request: NextRequest) {
  try {
    const body = await request.json()

    if (!body.id && !body.phoneNumberId) {
      return NextResponse.json(
        { success: false, error: 'Phone number ID is required' },
        { status: 400 }
      )
    }

    const phoneId = body.id || body.phoneNumberId

    // Get existing phone number
    const { data: existing, error: fetchError } = await supabase
      .from('PhoneNumber')
      .select('*')
      .eq('id', phoneId)
      .single()

    if (fetchError || !existing) {
      return NextResponse.json(
        { success: false, error: 'Phone number not found' },
        { status: 404 }
      )
    }

    // Build update object
    const updateData: Record<string, unknown> = {
      updatedAt: new Date().toISOString(),
    }

    // Updateable fields
    const fields = [
      'friendlyName', 'isPrimary', 'isDefault', 'assignedFor',
      'voiceUrl', 'voiceMethod', 'voiceFallbackUrl',
      'smsUrl', 'smsMethod', 'smsFallbackUrl',
      'voiceApplicationSid', 'smsApplicationSid', 'messagingServiceSid'
    ]

    fields.forEach(field => {
      if (body[field] !== undefined) {
        updateData[field] = body[field]
      }
    })

    // If setting this as primary, unset others
    if (body.isPrimary === true) {
      await supabase
        .from('PhoneNumber')
        .update({ isPrimary: false, updatedAt: new Date().toISOString() })
        .eq('subAccountId', existing.subAccountId)
        .neq('id', phoneId)
    }

    // If setting this as default, unset others
    if (body.isDefault === true) {
      await supabase
        .from('PhoneNumber')
        .update({ isDefault: false, updatedAt: new Date().toISOString() })
        .eq('subAccountId', existing.subAccountId)
        .neq('id', phoneId)
    }

    // Update in database
    const { data, error } = await supabase
      .from('PhoneNumber')
      .update(updateData)
      .eq('id', phoneId)
      .select()
      .single()

    if (error) throw error

    // If webhook URLs changed, update in Twilio
    if (body.voiceUrl || body.smsUrl) {
      const { data: twilioSubaccount } = await supabase
        .from('TwilioSubaccount')
        .select('twilioAccountSid, twilioAuthToken')
        .eq('id', existing.twilioSubaccountId)
        .single()

      if (twilioSubaccount && existing.phoneNumberSid) {
        try {
          const client = twilioService.getSubaccountClient(
            twilioSubaccount.twilioAccountSid,
            twilioSubaccount.twilioAuthToken
          )
          // Note: Would call client.updateNumber() here
        } catch (err) {
          console.error('Failed to update number in Twilio:', err)
        }
      }
    }

    return NextResponse.json({ success: true, data })
  } catch (error) {
    console.error('Error updating phone number:', error)
    return NextResponse.json(
      { success: false, error: 'Failed to update phone number' },
      { status: 500 }
    )
  }
}

// ==========================================
// DELETE - Release a phone number
// ==========================================

export async function DELETE(request: NextRequest) {
  try {
    const { searchParams } = new URL(request.url)
    const id = searchParams.get('id')
    const subAccountId = searchParams.get('subAccountId')

    if (!id) {
      return NextResponse.json(
        { success: false, error: 'Phone number ID is required' },
        { status: 400 }
      )
    }

    // Get existing phone number
    const { data: existing, error: fetchError } = await supabase
      .from('PhoneNumber')
      .select('*, TwilioSubaccount:twilioSubaccountId(*)')
      .eq('id', id)
      .single()

    if (fetchError || !existing) {
      return NextResponse.json(
        { success: false, error: 'Phone number not found' },
        { status: 404 }
      )
    }

    // Verify ownership if subAccountId provided
    if (subAccountId && existing.subAccountId !== subAccountId) {
      return NextResponse.json(
        { success: false, error: 'Unauthorized' },
        { status: 403 }
      )
    }

    // Release from Twilio
    if (existing.phoneNumberSid && existing.TwilioSubaccount) {
      const { data: agencyConfig } = await supabase
        .from('AgencyTwilioConfig')
        .select('*')
        .is('agencyId', null)
        .single()

      if (agencyConfig) {
        await twilioService.initialize(agencyConfig as AgencyTwilioConfig)
        
        try {
          await twilioService.releaseNumber(
            existing.TwilioSubaccount.twilioAccountSid,
            existing.TwilioSubaccount.twilioAuthToken,
            existing.phoneNumberSid
          )
        } catch (err) {
          console.error('Failed to release number from Twilio:', err)
          // Continue anyway - mark as released in database
        }
      }
    }

    // Update database (soft delete - keep record for billing history)
    const { error } = await supabase
      .from('PhoneNumber')
      .update({
        status: 'released',
        releasedAt: new Date().toISOString(),
        releaseReason: 'Released by user',
        isPrimary: false,
        isDefault: false,
        updatedAt: new Date().toISOString(),
      })
      .eq('id', id)

    if (error) throw error

    // If this was the primary, set another as primary
    if (existing.isPrimary) {
      const { data: nextNumber } = await supabase
        .from('PhoneNumber')
        .select('id')
        .eq('subAccountId', existing.subAccountId)
        .eq('status', 'active')
        .limit(1)
        .single()

      if (nextNumber) {
        await supabase
          .from('PhoneNumber')
          .update({ isPrimary: true, isDefault: true, updatedAt: new Date().toISOString() })
          .eq('id', nextNumber.id)
      }
    }

    return NextResponse.json({ success: true })
  } catch (error) {
    console.error('Error releasing phone number:', error)
    return NextResponse.json(
      { success: false, error: 'Failed to release phone number' },
      { status: 500 }
    )
  }
}
